import React, { useState, useEffect } from 'react';
import { TextField, Button, Grid, Box, Paper } from '@mui/material';
import axios from 'axios';

export default function Search() {

  const [first_name, setFirstName] = useState('');
  const [area, setArea] = useState('');
  const [district, setDistrict] = useState('');
  const [state, setState] = useState('');
  const [uniqueId, setUniqueId] = useState('');
  const [results, setResults] = useState([]);
  const [searchResults, setSearchResults] = useState([]);

  const isSearchDisabled = !first_name && !area && !district && !state && !uniqueId;
  const debounce = (func, delay) => {
    let debounceTimer;
    return function (...args) {
      const context = this;
      clearTimeout(debounceTimer);
      debounceTimer = setTimeout(() => func.apply(context, args), delay);
    };
  };
  const handleSearch = async () => {
    try {
      const response = await axios.get('http://localhost:5000/api/search', {
        params: {
          first_name,
          area,
          district,
          state,
          uniqueId
        }
      });
      //    console.log(response.data);
      setSearchResults(response.data);
    } catch (error) {
      console.error('Search failed:', error);
    }
  };
  const debouncedSearch = debounce(handleSearch, 500);
  //   useEffect(() => {
  //     debouncedSearch();
  // }, [first_name, area, district, state, uniqueId]);
  return (
    <Paper sx={{ padding: 2, marginTop: 2 }}>
      <Box component="form">
        <Grid container spacing={2}>
          <Grid item >
            <TextField
              label="Name"
              name="first_name"
              value={first_name}
              size="small"
              onChange={(e) => {
                setFirstName(e.target.value);
                debouncedSearch(); // Trigger the search as the user types
              }}
              variant="outlined"

            />
          </Grid>
          <Grid item >
            <TextField
              label="City/Town/Village"
              name="area"
              value={area} size="small"
              onChange={(e) => {
                setArea(e.target.value);
                debouncedSearch(); // Trigger the search as the user types
              }}
              variant="outlined"

            />
          </Grid>
          <Grid item >
            <TextField
              label="District"
              name="district"
              value={district} size="small"
              onChange={(e) => {
                setDistrict(e.target.value);
                debouncedSearch(); // Trigger the search as the user types
              }}
              variant="outlined"

            />
          </Grid>
          <Grid item >
            <TextField
              label="State"
              name="state"
              value={state} size="small"
              onChange={(e) => {
                setState(e.target.value);
                debouncedSearch(); // Trigger the search as the user types
              }}
              variant="outlined"

            />
          </Grid>
          <Grid item >
            <TextField
              label="Unique ID"
              name="uniqueId"
              value={uniqueId} size="small"
              onChange={(e) => {
                setUniqueId(e.target.value);
                debouncedSearch(); // Trigger the search as the user types
              }}
              variant="outlined"

            />
          </Grid>
          <Grid item >
            <Button
              variant="contained"
              color="primary"
              onClick={handleSearch}
              disabled={isSearchDisabled}

            >
              Search
            </Button>
          </Grid>
        </Grid>
      </Box>

      {/* Display search results */}
      <Box sx={{ marginTop: 4 }}>
        {searchResults.length > 0 ? (
          searchResults.map((result, index) => (
            <Paper key={index} sx={{ padding: 2, marginBottom: 2 }}>
              <Grid container spacing={2}>
                <Grid sx={{ padding: 2, marginBottom: 2 }}>
                  <strong>Name:</strong> {result.first_name}
                </Grid>
                <Grid sx={{ padding: 2, marginBottom: 2 }}>
                  <strong>City:</strong> {result.area}
                </Grid>
                <Grid sx={{ padding: 2, marginBottom: 2 }}>
                  <strong>District:</strong> {result.district}
                </Grid>
                <Grid sx={{ padding: 2, marginBottom: 2 }}>
                  <strong>State:</strong> {result.state}
                </Grid>
                <Grid sx={{ padding: 2, marginBottom: 2 }}>
                  <strong>Unique ID:</strong> {result.uniqueId}
                </Grid>
              </Grid>
            </Paper>
          ))
        ) : (
          <p>No results found.</p>
        )}
      </Box>
    </Paper>
  );
}
