import React, { useState, useEffect } from 'react';
import { styled } from '@mui/system';
import {
  TextField,
  Typography,
  Button,
  Container,
  Grid,
  Paper
} from '@mui/material';
 
import '../assets/css/style.css'


// Styled components
const PageContainer = styled(Container)({
  display: 'flex',
  minHeight: '100vh',
  backgroundColor: '#f5f5f5',
});

const LoginBox = styled(Paper)({
  padding: '30px',
  borderRadius: '8px',
  boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
  maxWidth: '400px',
  width: '100%',
});

const Logo = styled(Typography)({
  fontSize: '24px',
  fontWeight: 'bold',
  color: '#7c3aed',
  marginBottom: '20px',
});

const StyledTextField = styled(TextField)({
  marginBottom: '16px',
  width: '100%',
});

const LoginButton = styled(Button)({
  width: '100%',
  marginTop: '16px',
  backgroundColor: '#7c3aed',
  '&:hover': {
    backgroundColor: '#6d28d9',
  },
});

const SocialButton = styled(Button)({
  minWidth: '40px',
  margin: '0 5px',
});
const ResponsiveTextField = styled(TextField)({
  marginBottom: '16px',
  width: '100%',
});

const ErrorMessage = styled(Typography)({
  color: 'red',
  marginTop: '10px',
});



const LoginPage = () => {
  const [input, setInput] = useState('');
  const [email_id, setEmail] = useState('');
  const [phone_number, setPhone] = useState('');
  const [otp, setOtp] = useState('');
  const [message, setMessage] = useState('');
  const [timer, setTimer] = useState(90);
  const [isOTPSent, setIsOTPSent] = useState(false);

  useEffect(() => {
    if (isOTPSent && timer > 0) {
      const countdown = setInterval(() => {
        setTimer((prevTimer) => prevTimer - 1);
      }, 1000);
      return () => clearInterval(countdown);
    }
  }, [isOTPSent, timer]);

  const validateEmail = (email) => {
    const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return re.test(String(email).toLowerCase());
  };

  const validatePhone = (phone) => {
    const re = /^\d{10}$/;
    return re.test(phone);
  };
  const validateInput = (input) => {
    const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/; // Email regex pattern
    const phonePattern = /^\d{10}$/; // 10-digit phone number pattern

    if (emailPattern.test(input) || phonePattern.test(input)) {
      return true;
    } else {
      return false;
    }
  };
  const handleLogin = async (e) => {
    //  console.log('---------------',!validateEmail(input));
    e.preventDefault();
    if (!validateInput(input)) {
      setMessage('Please enter a valid email address or a 10-digit mobile number.');
      return;
    }

    if (!isOTPSent) {

      try {
        const response = await fetch('http://localhost:5000/api/login', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ input }),
        });
        const data = await response.json();
        console.log('-----data-----', data.data);
         
        if (data.message == 'Create Account First') {
          setMessage('Please create an account first.');
        } else {
          setPhone(input);
          setEmail(input);
          setMessage(data.message);
          let phone_number = input;
          let otp = data.data;
         /// setOtp(data.data);
          try {
            const response = await fetch('http://localhost:5000/api/update', {
              method: 'POST',
              headers: {
                'Content-Type': 'application/json',
              },
              body: JSON.stringify({  phone_number, otp }),
            });
            // console.log('User saved:', data);
            localStorage.setItem('isLoggedIn', 'true'); // Set isLoggedIn flag in localStorage
          setOtp(otp);
          } catch (err) {
           // console.error('Error saving user data:', err);
          }
          setIsOTPSent(true);
        }
        setTimer(90); // Reset timer
      } catch (error) {
        setMessage('Error sending OTP');
      }
    } else {
      verifyOTP(e);
    }
  };

  const handleResendOTP = () => {
    setIsOTPSent(false);
    setTimer(90);
    handleLogin(new Event('submit'));
  };

  const handleOTPChange = (e) => {
    setOtp(e.target.value);
  };

  const verifyOTP = async (e) => {
    e.preventDefault();

    if (otp.length !== 4) {
      setMessage('Please enter a valid 4-digit OTP.');
      return;
    }

    try {
      const response = await fetch('http://localhost:5000/api/verify-otp', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ email_id, phone_number, otp }),
      });
      const data = await response.json();
      sessionStorage.setItem('id', data.userId);
      if (data.redirectTo) {
        window.location.href = data.redirectTo;
      } else {
        setMessage(data.message);
      }
    } catch (error) {
      setMessage('Error verifying OTP');
    }
  };
  const handleInputChange = (e) => {
    setInput(e.target.value);
  };

  return (
    <PageContainer maxWidth={false}>
      <Grid container spacing={2} style={{ minHeight: '100vh' }}>
        <Grid item xs={12} md={6} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          {/* Add your illustration or statistics here */}
        </Grid>
        <Grid item xs={12} md={6} className="centered-grid-item">
          <LoginBox elevation={3}>
            <Logo>GOLD PETALS</Logo>
            <Typography variant="h5" gutterBottom className="mb-3">
              Welcome to Gold Petals! 
            </Typography>

            <form onSubmit={handleLogin} method='POST'>
              <ResponsiveTextField
                label="Mobile Number or Email ID"
                variant="outlined"
                type="text"
                value={input}
                onChange={handleInputChange}
                required
              />
              {/* <ResponsiveTextField
            label="Enter OTP"
            variant="outlined"
            type="text"
            value={otp}
            onChange={handleOTPChange}
          
          /> */}
              <Grid container justifyContent="space-between" alignItems="center" style={{ marginBottom: '16px' }}>
                {/* onClick={sendOTP} */}
                {isOTPSent && (
                  <>
                    <ResponsiveTextField
                      label="Enter OTP"
                      variant="outlined"
                      type="text"
                      value={otp}
                      onChange={handleOTPChange}
                      required
                    />
                    <Typography variant="body2" color="textSecondary">
                      {timer > 0
                        ? `Resend OTP in ${timer}s`
                        : <Typography component="span" color="primary" onClick={handleResendOTP}>Resend OTP</Typography>}
                    </Typography>
                  </>
                )}
                {message && <ErrorMessage>{message}</ErrorMessage>}
                <LoginButton variant="contained" type="submit">
                  {isOTPSent ? 'Verify OTP' : 'Get OTP'}
                </LoginButton>
              </Grid>
              {/* <LoginButton variant="contained" type="submit">
                Log In
              </LoginButton> */}
            </form>
            <Grid container direction="column" alignItems="center" style={{ marginTop: '24px' }}>
              <Typography variant="body2" gutterBottom>
                New on our platform? <Typography component="span" color="primary"><a href='/signup'> Create an account</a></Typography>
              </Typography>
              <Typography variant="body2" gutterBottom>
                <Typography component="span" color="primary">Guest Login</Typography>
              </Typography>
            </Grid>
          </LoginBox>
        </Grid>
      </Grid>
    </PageContainer>
  );
};

export default LoginPage;