import React, { useState, useEffect } from 'react';
import { TextField, Button, Box, Container, Typography, Avatar } from '@mui/material';
import axios from 'axios';

const EditProfile = () => {
  const [formData, setFormData] = useState({
    prefix: '',
    first_name: '',
    middle_name: '',
    last_name: '',
    display_name: '',
    phone: '',
    email_id: '',
    address: '',
    area: '',
    district: '',
    state: '',
    pin: '',
  });
  const [profilePicture, setProfilePicture] = useState(null);
  const [profilePicUrl, setProfilePicUrl] = useState('');

  useEffect(() => {
    // Fetch user data from the server on component mount
    const fetchUserData = async () => {
      try {
        const response = await axios.get('/api/user/profile');
        const userData = response.data;
        setFormData({
          prefix: userData.prefix || '',
          first_name: userData.first_name || '',
          middle_name: userData.middle_name || '',
          last_name: userData.last_name || '',
          display_name: userData.display_name || '',
          phone: userData.phone || '',
          email_id: userData.email_id || '',
          address: userData.address || '',
          area: userData.area || '',
          district: userData.district || '',
          state: userData.state || '',
          pin: userData.pin || '',
        });
        setProfilePicUrl(userData.profilePictureUrl || '');
      } catch (error) {
        console.error('Failed to fetch user data', error);
      }
    };

    fetchUserData();
  }, []);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleProfilePicChange = (e) => {
    if (e.target.files && e.target.files[0]) {
      setProfilePicture(e.target.files[0]);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const formDataToSubmit = new FormData();
    for (const key in formData) {
      formDataToSubmit.append(key, formData[key]);
    }
    if (profilePicture) {
      formDataToSubmit.append('profilePicture', profilePicture);
    }

    try {
      await axios.post('/api/user/update-profile', formDataToSubmit, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
      });
      alert('Profile updated successfully!');
    } catch (error) {
      console.error('Failed to update profile', error);
    }
  };

  return (
    <Container maxWidth="sm">
      <Box sx={{ mt: 4 }}>
        {/* <Typography variant="h4" gutterBottom>Edit Profile</Typography> */}
        <Box
          component="form"
          noValidate
          onSubmit={handleSubmit}
          sx={{ mt: 2 }}
        >
          <Avatar
            src={profilePicUrl ? profilePicUrl : '/default-profile.png'}
            sx={{ width: 100, height: 100, mb: 2 }}
          />
          <Button variant="contained" component="label">
            Upload Profile Picture
            <input
              type="file"
              hidden
              onChange={handleProfilePicChange}
            />
          </Button>
          <TextField
            label="Prefix"
            name="prefix"
            value={formData.prefix}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <TextField
            label="First Name"
            name="first_name"
            value={formData.first_name}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Middle Name"
            name="middle_name"
            value={formData.middle_name}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Last Name"
            name="last_name"
            value={formData.last_name}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Display Name"
            name="display_name"
            value={formData.display_name}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Phone"
            name="phone"
            value={formData.phone}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Email ID"
            name="email_id"
            value={formData.email_id}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Address"
            name="address"
            value={formData.address}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <TextField
            label="Area"
            name="area"
            value={formData.area}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <TextField
            label="District"
            name="district"
            value={formData.district}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <TextField
            label="State"
            name="state"
            value={formData.state}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <TextField
            label="PIN"
            name="pin"
            value={formData.pin}
            onChange={handleInputChange}
            fullWidth
            margin="normal"
          />
          <Button type="submit" variant="contained" color="primary" sx={{ mt: 2 }}>
            Save Changes
          </Button>
        </Box>
      </Box>
    </Container>
  );
};

export default EditProfile;
