import React from 'react';
import { useNavigate } from 'react-router-dom';

const NotificationsMenu = () => {
  const navigate = useNavigate();

  const handleLogout = () => {
    // Remove the isLoggedIn flag from localStorage
    localStorage.removeItem('isLoggedIn');
    sessionStorage.clear();
    // Redirect to the login page
    navigate('/login');
  };

  const notifications = [
    { text: 'Profile', route: '/profile' },
    { text: 'Help', route: '/help' },
    { text: 'Logout', route: handleLogout }, // Use the logout function directly
  ];

  const handleNotificationClick = (route) => {
    if (typeof route === 'function') {
      route(); // Call the logout function
    } else {
      navigate(route); // Navigate to the route
    }
  };

  return (
    <ul>
      {notifications.map((notification, index) => (
        <li key={index}>
          <button onClick={() => handleNotificationClick(notification.route)}>
            {notification.text}
          </button>
        </li>
      ))}
    </ul>
  );
};

export default NotificationsMenu;
