import React, { useState } from 'react';
import axios from 'axios';
import { styled } from '@mui/system';
import {
  TextField,
  Typography,
  Button,
  Container,
  Grid,
  Paper,
  MenuItem,
} from '@mui/material';
import '../assets/css/style.css';

// Styled components
const PageContainer = styled(Container)({
  display: 'flex',
  minHeight: '100vh',
  backgroundColor: '#f5f5f5',
});

const RegisterBox = styled(Paper)({
  padding: '30px',
  borderRadius: '8px',
  boxShadow: '0px 0px 10px rgba(0, 0, 0, 0.1)',
  maxWidth: '400px',
  width: '100%',
});

const Logo = styled(Typography)({
  fontSize: '24px',
  fontWeight: 'bold',
  color: '#7c3aed',
  marginBottom: '20px',
});

const StyledTextField = styled(TextField)({
  marginBottom: '16px',
  width: '100%',
});

const RegisterButton = styled(Button)({
  width: '100%',
  marginTop: '16px',
  backgroundColor: '#7c3aed',
  '&:hover': {
    backgroundColor: '#6d28d9',
  },
});

const ResponsiveTextField = styled(TextField)({
  marginBottom: '16px',
  width: '100%',
});

const RegisterForm = () => {
  const [formData, setFormData] = useState({
    prefix: '',
    first_name: '',
    middle_name: '',
    last_name: '',
    display_name: '',
    phone: '',
    email_id: '',
    address: '',
    area: '',
    district: '',
    state: '',
    pin: '',
  });

  // const handleInputChange = (e) => {
  //   const { name, value } = e.target;
  //   setFormData({ ...formData, [name]: value });
  // };

  // const handleSubmit = (e) => {
  //   e.preventDefault();
  //   // Perform form submission actions
  // };
  //========================================================================== 

  const [isPhoneVerified, setIsPhoneVerified] = useState(false);
  const [isEmailVerified, setIsEmailVerified] = useState(false);
  const [otpSentTo, setOtpSentTo] = useState(null); // 'phone' or 'email'

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const validatephone_number = (phoneNumber) => {
    return /^\d{10}$/.test(phoneNumber);
  };

  const validateEmail = (email) => {
    return /^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(email);
  };

  const handleSendOtp = (type) => {
    const data = type === 'phone' ? { phoneNumber: formData.phoneNumber } : { email: formData.email };

    axios.post('/api/send-otp', data)
      .then((response) => {
        setOtpSentTo(type);
        alert('OTP sent successfully!');
      })
      .catch((error) => {
        console.error('Error sending OTP:', error);
      });
  };

  const handleVerifyOtp = (type) => {
    const otp = type === 'phone' ? formData.otpPhone : formData.otpEmail;
    const data = { otp, type, value: formData[type === 'phone' ? 'phoneNumber' : 'email'] };

    axios.post('/api/verify-otp', data)
      .then((response) => {
        if (type === 'phone') {
          setIsPhoneVerified(true);
        } else {
          setIsEmailVerified(true);
        }
        alert('OTP verified successfully!');
      })
      .catch((error) => {
        console.error('Error verifying OTP:', error);
        alert('OTP verification failed. Please try again.');
      });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      const response = await axios.post('http://localhost:5000/api/signup', formData);
      console.log('User saved:', response.data);
    } catch (err) {
      console.error('Error saving user data:', err);
    }
  };



  return (
    <PageContainer maxWidth={false}>
      <Grid container spacing={2} style={{ minHeight: '100vh' }}>
        <Grid item xs={12} md={6} style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
          {/* Add your illustration or statistics here */}
        </Grid>
        <Grid item xs={12} md={6} className="centered-grid-item">
          <RegisterBox elevation={3}>
            <Logo>GOLD PETALS</Logo>
            <Typography variant="h5" gutterBottom className="mb-3">
              Create Your Account
            </Typography>

            <form onSubmit={handleSubmit}>
              <ResponsiveTextField
                select
                label="Prefix"
                name="prefix"
                value={formData.prefix}
                onChange={handleInputChange}
                variant="outlined"
                required
              >

                <MenuItem value="Mr">Mr</MenuItem>
                <MenuItem value="Mrs">Mrs</MenuItem>
                <MenuItem value="Ms">Ms</MenuItem>
                <MenuItem value="Miss">Miss</MenuItem>
                <MenuItem value="Master">Master</MenuItem>
              </ResponsiveTextField>

              <ResponsiveTextField
                required
                label="First Name"
                name="first_name"
                value={formData.first_name}
                onChange={handleInputChange}
                variant="outlined"

              />

              <ResponsiveTextField
                label="Middle Name"
                name="middle_name"
                value={formData.middle_name}
                onChange={handleInputChange}
                variant="outlined"
              />

              <ResponsiveTextField
                required
                label="Last Name"
                name="last_name"
                value={formData.last_name}
                onChange={handleInputChange}
                variant="outlined"
              />

              <ResponsiveTextField
                required
                label="Display Name"
                name="display_name"
                value={formData.display_name}
                onChange={handleInputChange}
                variant="outlined"
              />

              <ResponsiveTextField
                required
                label="Phone Number"
                name="phone_number"
                value={formData.phone_number || ''}
                onChange={(e) => {
                  const phone_number = e.target.value;
                  if (/^\d{0,10}$/.test(phone_number)) {
                    handleInputChange(e);
                  }
                }}
                variant="outlined"
                fullWidth
                disabled={isPhoneVerified}
                inputProps={{
                  maxLength: 10, // Limit the input to 10 characters
                  inputMode: 'numeric', // Ensures the numeric keyboard on mobile devices
                }}

              />
              {!isPhoneVerified && (
                <>
                  <Button
                    variant="contained"
                    onClick={() => validatephone_number(formData.phone_number) && handleSendOtp('phone')}
                    disabled={!validatephone_number(formData.phone_number)}
                  >
                    Get OTP for Phone
                  </Button>
                  {otpSentTo === 'phone' && (
                    <>
                      <TextField
                        required
                        label="Enter OTP"
                        name="otpPhone"
                        value={formData.otpPhone}
                        onChange={handleInputChange}
                        variant="outlined"
                        fullWidth
                      />
                      <Button
                        variant="contained"
                        onClick={() => handleVerifyOtp('phone')}
                      >
                        Verify Phone OTP
                      </Button>
                    </>
                  )}
                </>
              )}
              <ResponsiveTextField
                required
                label="Email"
                name="email_id"
                value={formData.email_id}
                onChange={handleInputChange}
                variant="outlined"
                fullWidth
                disabled={isEmailVerified}
              />
              {!isEmailVerified && (
                <>
                  <Button
                    variant="contained"
                    onClick={() => validateEmail(formData.email_id) && handleSendOtp('email_id')}
                    disabled={!validateEmail(formData.email_id)}
                  >
                    Get OTP for Email
                  </Button>
                  {otpSentTo === 'email_id' && (
                    <>
                      <TextField
                        required
                        label="Enter OTP"
                        name="otpEmail"
                        value={formData.otpEmail}
                        onChange={handleInputChange}
                        variant="outlined"
                        fullWidth
                      />
                      <Button
                        variant="contained"
                        onClick={() => handleVerifyOtp('email_id')}
                      >
                        Verify Email OTP
                      </Button>
                    </>
                  )}
                </>
              )}
              <ResponsiveTextField
                required
                label="House No./ Plot No./ Sector"
                name="address"
                value={formData.address}
                onChange={handleInputChange}
                variant="outlined"
              />

              <ResponsiveTextField
                required
                label="Village/ City/ Town"
                name="area"
                value={formData.area}
                onChange={handleInputChange}
                variant="outlined"
              />

              <ResponsiveTextField
                required
                label="District"
                name="district"
                value={formData.district}
                onChange={handleInputChange}
                variant="outlined"
              />

              <ResponsiveTextField
                required
                label="State"
                name="state"
                value={formData.state}
                onChange={handleInputChange}
                variant="outlined"
              />

              <ResponsiveTextField
                required
                label="Pin"
                name="pin"
                value={formData.pin}
                onChange={handleInputChange}
                variant="outlined"
              />

              <RegisterButton variant="contained" type="submit">
                Register
              </RegisterButton>
            </form>

            <Grid container direction="column" alignItems="center" style={{ marginTop: '24px' }}>
              <Typography variant="body2" gutterBottom>
                Already have an account?{' '}
                <Typography component="span" color="primary">
                  <a href="/">Log In</a>
                </Typography>
              </Typography>
            </Grid>
          </RegisterBox>
        </Grid>
      </Grid>
    </PageContainer>
  );
};

export default RegisterForm;
